
$gray: #ebebeb;
$gray-base: #4c4c4d;
$black: #000;
$star: #00dcdc;

$primary-color: $gray;
$brand-success: #5cb85c;
$brand-info: #5bc0de;
$brand-warning: #f0ad4e;
$brand-danger: #d9534f;
$gray-light: #999;
$gray-middle: #ddd;
$gray-dark: #555;
$red: rgb(255, 0, 0);

/*== Scaffolding*/
/**/
/*## Settings for some of the most global styles.*/

/*** Background color for `<body>`.*/
$body-bg: #fff;
/*** Global text color on `<body>`.*/
$text-color: $gray-base;
/*** Global textual link color.*/
$link-color: $primary-color;
/*** Link hover color set via `darken()` function.*/
$link-hover-color: darken($link-color, 15%);
//** Link hover decoration.
$link-hover-decoration: underline !default;

//== Paths Paths
//
//## Fonts and imaage paths
$font-path-url: "../fonts";
$img-path-url: "../imgs";

//== Typography
//
//## Font, line-height, and color for body text, headings, and more.
$font-family-primary: "Avenir", sans-serif;
$font-family-secondary: "Georgia", serif;
$font-karla: "Karla", sans-serif;
$font-size-base: 14px;
$line-height-base: 1.428571429; /* 20/14*/
$font-weight-base: 400;

$font-size-h1: 3.6rem;
$font-size-h2: 3rem;
$font-size-h3: 2.4rem;
$font-size-h4: 1.8rem;
$font-size-h5: 1.4rem;
$font-size-h6: 1.2rem;

/*** By default, this inherits from the `<body>`.*/
$headings-font-family: inherit !default;
$headings-font-weight: 900 !default;
$headings-line-height: 1.1 !default;
$headings-color: inherit !default;

/* spacing */
$padding: 2rem;
$margins: 1.5rem;
$content-padding: 60px 0;
$content-padding-mobile: 30px 0;

/*== Buttons*/
/**/
/*## For each of Theme buttons, define text, background and border color.*/
$button-font-weight: normal;

$button-font-family: $font-family-primary;
$button-font-size: 14px;
$button-base-vertical: 6px;
$button-base-horizontal: 12px;

$btn-theme-color: #fff;
$btn-theme-bg: $primary-color;
$btn-theme-border: darken($btn-theme-bg, 5%);

// Allows for customizing button radius independently from global border radius
$btn-border-radius-base: 0 !default;

//== Grid system
//
//## Define your custom responsive grid.

//** Padding between columns. Gets divided in half for the left and right.
$gutter: 30px;

/*== Overlays*/
/**/
/*## Color overlays over images on banner or image grid galleries*/
$color-image-overlay: $black;
$opacity-image-overlay: 0.6;


// $media-breakpoints
$xlarge-breakpoint:1140px;
$large-breakpoint:1024px;
$laptop-breakpoint:992px;
$ipad-breakpoint:960px;
$medium-breakpoint:830px;
$tablet-breakpoint:768px;
$mobile-breakpoint:578px;
$small-breakpoint:491px;